.random-planet {
  display: flex;
  padding: 1rem;
  background-color: #303030;
  .planet-image {
    width: 150px;
    height: 150px;
    border-radius: 10px;
    margin-right: 1rem;
  }
  .list-group {
    margin: 1rem 0 0 1rem;
    border-radius: 0;
  }
  .list-group-item {
    padding: 0.25rem;
    border-width: 0 0 1px;
    .term {
      margin-right: 0.5rem;
    }
  }
}
